<template>
  <div>
    <div class="table-operator" style="margin-bottom: 16px;">
      <a-row>
        <a-col>
          <a-row type="flex" justify="space-between">
            <span>
              <a-button icon="sync" :disabled="loading" @click="handleRefresh" style="margin-right: 10px;" title="刷新"></a-button>
              <a-button
                type="primary"
                style="margin-right: 10px;"
                icon="plus"
                @click="handleSingleMenuClick('create')"
              >
                创建
              </a-button>
              <a-button
                type="danger"
                style="margin-right: 10px;"
                icon="delete"
                @click="handleMultiMenuClick('delete')"
              >
                删除
              </a-button>
            </span>

            <span style="display: inline-flex;">
              <a-input-group compact class="compact-search-input">
                <a-select @change="v => searchValues.type = v" v-model="searchValues.type" style="width: 90px!important;">
                  <a-select-option
                    v-for="(value, key) in types"
                    :value="key"
                    :key="key"
                  >
                    {{ value }}
                  </a-select-option>
                </a-select>
                <a-input
                  style="width: 200px"
                  @pressEnter="handleDATA"
                  v-model="searchValues.inputValue"
                  :placeholder="`请输入${types[searchValues.type]}信息`"
                >
                  <a-icon v-if="searchValues.inputValue" slot="suffix" type="close-circle" @click="searchValues.inputValue = ''" />
                </a-input>
              </a-input-group>
              <a-button type="primary" @click="handleDATA" style="margin-left: 8px">
                搜索
                <a-icon type="search" />
              </a-button>
            </span>
          </a-row>
        </a-col>
      </a-row>
    </div>
    <a-alert type="info" showIcon style="margin-bottom: 16px;">
      <div slot="message">
        已选择&nbsp;
        <a style="font-weight: 600">{{ selectedRowKeys.length }}</a>&nbsp;&nbsp;项
        <a style="margin-left: 24px" @click="handleClearSelected">清空</a>
      </div>
    </a-alert>
    <a-table
      :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onTableSelectChange}"
      :columns="columns"
      :rowKey="record => record.id"
      :dataSource="data"
      :pagination="pagination"
      :loading="loading"
      @change="handleTableChange"
    >
      <template slot="internal" slot-scope="internal">
        <p v-show="Array.isArray(internal)" v-for="item in internal" :key="item.id">
          {{ item.name + ' : ' }}{{ item.cidr }}
        </p>
        <p v-show="!Array.isArray(internal)">{{ internal }}</p>
      </template>
      <template slot="operation" slot-scope="text, record">
        <a-dropdown>
          <a-menu slot="overlay" @click="handleSingleMenuClick($event.key, record)">
            <a-menu-item
              v-for="item in record.singleMenuOptions"
              :key="item.id"
            >
              {{ item.name }}
            </a-menu-item>
          </a-menu>
          <a-button>
            操作
            <a-icon type="down" />
          </a-button>
        </a-dropdown>
      </template>
    </a-table>

    <create-modal />
    <edit-modal />
    <clear-modal />
    <delete-modal />
    <delete-router />
    <gateway />
    <attach />
    <detach />
  </div>
</template>

<script>
import CreateModal from "./Modal/Create";
import EditModal from "./Modal/Edit";
import ClearModal from "./Modal/Clear";
import DeleteModal from "./Modal/Delete";
import DeleteRouter from "./Modal/DeleteRouter";
import Gateway from "./Modal/Gateway";
import Attach from "./Modal/Attach";
import Detach from "./Modal/Detach";

import tablePageMixins from "@/mixins/tablePageMixins";

import { getRouterList as getList } from "@/api/network/router";

import { getNetworkList } from "@/api/network/network";

import router from '@/i18n/zh/router'
const statusDicts = router.router.status;

export default {
  mixins: [tablePageMixins],
  components: {
    CreateModal,
    EditModal,
    ClearModal,
    DeleteModal,
    DeleteRouter,
    Gateway,
    Attach,
    Detach,
  },

  data() {
    return {
      getList,
      module: "network",
      id: "router",
      name: "路由",
      confirmLoading: false,

      networkList: [],
      searchValues: {
        type: 'name',
        inputValue: '',
      },
      types: {
        name: '名称',
        shortID: 'ID',
      },

    };
  },
  mounted () {
    this.fetchNetworkList();
  },
  methods: {
    async fetchNetworkList () {
      try {
        const resp = await getNetworkList();
        this.networkList = resp.data.filter(item => item.isRouterExternal);
      } catch (error) {

      }
    },
    __handleTransformToZh (status) {
      return statusDicts[status] || status
    },
    handleParseData (data) {
      data.forEach(item => {
        item.status_zh = this.__handleTransformToZh(item.status);

        // 操作菜单权限过滤
        const options = JSON.parse(JSON.stringify(this.singleMenuOptions));

        item.singleMenuOptions = options;
      })
      return data;
    },
    handleSingleMenuClick(key, record) {
      if (key === '') {

      }
      // 单项操作
      this.currRecord = record;
      this.handleShowModal(key);
    },
  }
};
</script>

